import React from 'react'
import s from './ProfileInfo.module.css'
import Preloader from '../../../common/Preloader/Preloader'
import userPhoto from '../../../../assets/images/defaultIcon.png'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'

const contactMaker = (contact, link) => {
    if (!link) {
        return (
            null
        )
    }
    return (
        <div>
            {contact}: {link}
        </div>
    )
}

const ProfileInfo = ({ profile, status, updateStatus, isOwner, savePhoto }) => {
    if (!profile) {
        return (
            <Preloader />
        )
    }

    const onMainPhotoSelected = (event) => {
        if (event.target.files.length) {
            savePhoto(event.target.files[0])
        }
    }

    return (
        <div>
            {/* <div>
                <img src='https://i.pinimg.com/originals/eb/22/6c/eb226c61b2925659cfd02f6b38ccf921.jpg' alt={'fail load'}></img>
            </div> */}
            <div className={s.descriptionBlock}>
                <img src={profile.photos.large || userPhoto} alt='fail load' className={s.mainPhoto}></img>
                <div>
                    <div>
                        name: {profile.fullName}
                    </div>
                    <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
                    {isOwner && <input type={'file'} onChange={onMainPhotoSelected} />}
                    <div>
                        about me: {profile.aboutMe}
                    </div>
                    <div>
                        {profile.lookingForAJob ? <div>Finding a work</div> : <div>Is't finding a work</div>}
                    </div>
                    <div>{profile.lookingForAJob ? <div> Description for a finding work: {profile.lookingForAJobDescription}</div> : null} </div>
                    <div>
                        My contacts:
                        {contactMaker('facebook', profile.contacts.facebook)}
                        {contactMaker('website', profile.contacts.website)}
                        {contactMaker('vk', profile.contacts.vk)}
                        {contactMaker('twitter', profile.contacts.twitter)}
                        {contactMaker('instagram', profile.contacts.instagram)}
                        {contactMaker('youtube', profile.contacts.youtube)}
                        {contactMaker('github', profile.contacts.github)}
                        {contactMaker('mainLink', profile.contacts.mainLink)}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo